import React,{useContext} from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import { Context } from './../data/contextProvider';
const Project =(props)=>{
  const value=useContext(Context);
  const {id,name,img}=props.project;
return(
    <React.Fragment>
      <ProjectWrapper className="col-9 mx-auto col-md-6 col-lg-3 my-3" >
          <div className="project">
          <div className="img-container p-5"
                onClick={()=>{value.handleDetail(id)}}>    
        <Link to="/details">
        <img src={img} alt="" className="card-img-top" />
        </Link>
        </div>
        <div className="card-footer d-flex justify-content-between">
            <p className="align-self-center mb-0" style={{fontFamily:"'Shadows Into Light', cursive",
                                                            color:"#F54BA3"}}>{name}</p>
            </div>
        </div>
      </ProjectWrapper>
    </React.Fragment>
);
}
export default Project

export const ProjectWrapper=styled.div`
.project{
    border-color: transparent;
    transition: all 1s linear;
}
.card-footer {
  background: transparent;
  border-top: transparent;
  transition: all 1s linear;
}
&:hover {
  .project {
    border: 2px solid rgba(0, 0, 0, 0.2); ;
    box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.2);;
  }
  .card-footer {
    background:rgba(247, 247, 247);
  }
}
  .card-img-top {
    transition: all 1s linear;
  }
  .img-container {
    position: relative;
  }
 
  .img-container:hover .card-img-top {
    transform: scale(1.2);
  }
  .img-container:hover {
    transform: translate(0, 0);
  }
  
`;
